import React, { useState, useEffect } from "react";
import mock09 from '../assets/images/modar-kajo-RtawfHplLrc-unsplash.jpg';
import mock10 from '../assets/images/mock10.png';
import '../assets/styles/Project.scss';

interface ProjectData {
  title: string;
  href: string;
  img: string;
  desc: string;
}

const projects: ProjectData[] = [
  {
    title: "PainoBot",
    href: "https://www.filmate.club/",
    img: mock09,
    desc:
      "Developed movie finder app with semantic search and sentiment analysis using OpenAI GPT-3.5 Turbo, Qdrant, React, and Flask."
  },
  {
    title: "High Speed Chase",
    href: "https://yujisatojr.itch.io/highspeedchase",
    img: mock10,
    desc:
      "Designed, developed, and launched a 3D multiplayer racing game with C# and Unity. Available on Itch.io for worldwide gamers."
  }
];

export default function Project({ mode }: { mode: 'light' | 'dark' }) {
  const [selected, setSelected] = useState<number | null>(null);

    // Lock body scroll when modal is open:
    useEffect(() => {
        document.body.style.overflow = selected !== null ? "hidden" : "";
        return () => {
          document.body.style.overflow = "";
        };
      }, [selected]);

  return (
    <div className={`projects-container ${mode}`} id="projects">
      <h1>Projects</h1>
      <div className="projects-grid">
        {projects.map((p, i) => (
          <div
            key={p.title}
            className="project"
            onClick={() => setSelected(i)}
          >
            <img src={p.img} className="zoom" alt={p.title} />
            <h2>{p.title}</h2>
            <p>{p.desc}</p>
          </div>
        ))}
      </div>

      {selected !== null && (
        <div className="project-modal" onClick={() => setSelected(null)}>
          <div className="modal-backdrop" />
          <div className={`modal-card ${mode}`} onClick={e => e.stopPropagation()}>
            <button className="modal-close" onClick={() => setSelected(null)}>
              &times;
            </button>
            <img src={projects[selected].img} alt={projects[selected].title} />
            <h2>{projects[selected].title}</h2>
            <p>{projects[selected].desc}</p>
            <a href={projects[selected].href} target="_blank" rel="noreferrer">
              View Live
            </a>
          </div>
        </div>
      )}
    </div>
  );
}